import React from "react"
import {NavBar, InputItem, Button, TextareaItem, WhiteSpace} from "antd-mobile"
import IconSelector from "../../components/icon-selector/icon-selector"
import {connect} from "react-redux"
import {update} from "../../redux/actions"
import {Redirect} from "react-router-dom"

class HaveaRoomInfo extends React.Component{
    state={
        room:"",
        location:"",
        budget:"",
        intro:"",
        header:""
    }
    
    handleChange(name,val){
        this.setState({
            [name]:val
        })
    }

    selectAvatar=(icon)=>{
        this.setState({
           header:icon
        })
    }

    save(){
        this.props.update(this.state)
    }

    render(){
        const {header,type}=this.props.user
        if(header){
            const path = type==="haveaRoom"?"/haveaRoom":"needaRoom"
            return <Redirect to={path} />
        }

        return(
            <div id="haveaRoom-info">
                <NavBar>COMPLETE PROFILE</NavBar>
                <IconSelector selectAvatar={this.selectAvatar}/>
                <InputItem onChange={(val)=>{this.handleChange("location",val)}}>Location: </InputItem>
                <InputItem onChange={(val)=>{this.handleChange("budget",val)}}>Budget: </InputItem>
                <TextareaItem title="My room: " autoHeight onChange={(val)=>{this.handleChange("room",val)}}></TextareaItem>
                <TextareaItem title="About me: " autoHeight onChange={(val)=>{this.handleChange("intro",val)}}></TextareaItem>
                <WhiteSpace />
                <Button type="primary" onClick={()=>{this.save()}}>SAVE</Button>
            </div>
        )
    }
}

export default connect(
    state=>({user:state.user}),
    {update}
)(HaveaRoomInfo)