import React from "react"
import {List, Grid} from "antd-mobile"
import PropTypes from "prop-types"

export default class IconSelector extends React.Component{
    static propTypes={
        selectAvatar: PropTypes.func.isRequired
    }

    state={
        icon:null
    }

    constructor(props){
        super(props)
        this.icons=[]
        const iconList=["icon1","icon2","icon3","icon4","icon5","icon6","icon7","icon8","icon9","icon10","icon11","icon12","icon13","icon14","icon15","icon16","icon17","icon18","icon19","icon20"]
        for(let i=0;i<iconList.length;i++){
            this.icons.push({
                text:iconList[i],
                icon:require(`../../assets/icons/${iconList[i]}.png`)
            })
        }
    }

    selectIcon({text,icon}){
        this.setState({icon})
        this.props.selectAvatar(text)
    }

    render(){
        const header = this.state.icon?
        <div>Icon Selected: <img alt="icon" style={{height:"50px",width:"50px"}} src={this.state.icon} /></div>:
        <div>Please select an avatar</div>
        return(
            <List renderHeader={()=>header}>
                <Grid itemStyle={{ height: '60px'}} renderItem={dataItem=>(<div style={{ padding: '12.5px' }}>
          <img src={dataItem.icon} style={{ width: '40px', height: '40px' }} alt="" />
                </div>)} data={this.icons} columnNum={5} onClick={(e)=>this.selectIcon(e)}></Grid>
            </List>
        )
    }
}